.sales-report-container {
  position: relative;
  color: #000;
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 0.25em;
  border-bottom: none;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.5);
}

.sales-report-container h2 {
  color: #000;
  font-size: 2rem;
  text-transform: none;
}

.sales-report-container h3 {
  color: #000;
  font-size: 1.5rem;
  text-transform: none;
}

.report-dates {
  color: rgb(153, 153, 153);
}
.report-incomes {
  font-weight: bold;
}

.delete-report {
  position: absolute;
  top: 2rem;
  right: 2rem;
}

.report-deleted {
  display: none;
}
